export default "<template>\n  <figure>\n    <img src=\"https://cdn.jsdelivr.net/gh/turkyden/image-hover/placeholder.png\" />\n    <figcaption>\n      Hello World ! 🐶\n    </figcaption>\n  </figure>\n</template>\n\n<style scoped>\nfigure {\n  position: relative;\n  display: inline-block;\n  margin: 0;\n  max-width: 100%;\n  background-color: #2266a5;\n  color: #fff;\n  overflow: hidden;\n  -webkit-backface-visibility: hidden;\n  backface-visibility: hidden;\n  -moz-osx-font-smoothing: grayscale;\n  -webkit-transform: translateZ(0);\n  transform: translateZ(0);\n}\n\nfigure > img {\n  vertical-align: top;\n  max-width: 100%;\n}\n\nfigure figcaption {\n  background-color: #135796;\n  padding: 30px;\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n}\n\nfigure,\nfigure:before,\nfigure:after,\nfigure *,\nfigure *:before,\nfigure *:after {\n  box-sizing: border-box;\n  transition: all 0.35s ease;\n}\n\n/** shutter-out- **/\nfigure:before {\n  background: #135796;\n  position: absolute;\n  content: '';\n  transition-delay: 0.105s;\n}\n\nfigure figcaption {\n  opacity: 0;\n  transition-delay: 0s;\n}\n\nfigure:hover:before {\n  transition-delay: 0s;\n}\n\nfigure:hover figcaption {\n  opacity: 1;\n  transition-delay: 0.105s;\n}\n\n/** shutter-out-diag-1 **/\nfigure:before {\n  top: 50%;\n  bottom: 50%;\n  left: -35%;\n  right: -35%;\n  transform: rotate(45deg);\n}\n\nfigure:hover:before {\n  top: -35%;\n  bottom: -35%;\n}\n</style>\n";